<template>
  <div id="index">
     <yd-pullrefresh :callback="loadList" ref="pullrefreshDemo">
        <yd-list theme="5">
            <yd-list-item v-for="(item, key) in list" :key="key" type="link" :href="'/detail/'+item.id">
                <img slot="img" :src="item.images.small">
                <span slot="title">{{item.original_title}}</span>
                <yd-list-other slot="other">
                    <div>
                        <span class="list-price">{{item.casts[0].name}}</span>
                        <span class="list-del-price">{{item.year}}</span>
                    </div>
                    <div>{{item.directors[0].name}}</div>
                </yd-list-other>
            </yd-list-item>
        </yd-list>
    </yd-pullrefresh>
  </div>
</template>

<script>
    export default {
        data(){
            return{
               start:1,
               list:[]
            }
        },
        computed:{
        	
        },
        created(){
            var _this = this;
            this.$axios.get('/api/movie/in_theaters',{params:{start:this.start,count:10,city:'广州'}}).then(function(res){
                 _this.list = res.data.subjects;
            }).catch(function(err){
                console.log(err);
            })
        },
        methods:{
            loadList() {
                
            }
        }
    }
</script>

<style>

</style>


