import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)
export default new Vuex.Store({
    //存放组件之间共享的数据
    //state属性用来存储状态值
    state: {
        count: 0,
        sah: true,
        text: '显示',
        arr: ['1', '2', '3'],
        a: ''
    },
    //获取数据的方法
    //getters属性用来获取状态值
    getters: {
        count(state) {
            return state.count;
        },
        odd(state) {
            return state.count % 2 == 0 ? '偶数' : '奇数';
        }
    },
    mutations: {
        increment(state) {
            state.count++;
        },
        decrement(state) {
            state.count--;
            if (state.count < 0) {
                state.count = 0;
            }
        },
        click(state, a) {
            state.a = a;
        }
    }
})


